<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn 
        depressed
        v-on="on"
        class="mr-3"
        color="primary">
        <v-icon class="mr-3">mdi-plus-circle</v-icon>
        Tambah data
      </v-btn>
    </template>

    <v-card>
      <v-card-title class="py-2">
        Tambah Upload File
      </v-card-title>
      <v-divider></v-divider>
      <v-form v-model="valid" ref="form">
        <v-card-text class="pa-6 pb-0">
          <v-text-field
            v-model="dataUpload.nama"
            label="Nama file"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-select
            v-model="dataUpload.acl"
            :items="items"
            label="Pilihan akses"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-select>
          <v-file-input
            accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
            outlined
            dense
            prepend-icon="mdi-file"
            show-size
            counter
            v-model="dataUpload.url_file"
            :rules="requiredRule"
            label="File upload"
          ></v-file-input>
          <v-file-input
            accept="image/*"
            outlined
            dense
            prepend-icon="mdi-image"
            show-size
            counter
            v-model="dataUpload.url_gambar"
            :rules="requiredRule"
            label="File gambar"
          ></v-file-input>
          <Notif :status="status" :msg="msg" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            :loading="isLoading"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'
  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        dataUpload: {
          nama: '',
          acl: '',
          url_gambar: null,
          url_file: null
        },
        items: ['Unit', 'Admin'],
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Data berhasil ditambahkan',
          error: 'Data gagal ditambahkan',
          visible: false
        }
      }
    },

    components: { Notif },

    computed: {
      ...mapState('dataFileupload', ['isLoading', 'status']),
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          // console.log(this.dataFileupload)
          let data = new FormData()
          data.append('nama', this.dataUpload.nama)
          data.append('acl', this.dataUpload.acl)
          data.append('url_gambar', this.dataUpload.url_gambar)
          data.append('url_file', this.dataUpload.url_file)
          this.$store.dispatch('dataFileupload/postDataFileupload', this.dataFileupload)
          this.$store.dispatch('dataFileupload/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
      }
    }
  }
</script>
